import React from 'react'
import './Advertisement.css'
import Navbar from '../navbar/Navbar'
import { Container, Row, Col } from 'react-bootstrap'
import plusIcon from '../../images/plus.png'

const Advertisement = () => {
    return (
        <>
            <Navbar />

            <section>
                <Container>
                    <div className="add_product">
                        <h2>Informacje Podstawowe</h2>

                        <form>
                            <Row>

                                <Col lg={12} className="set_title">
                                    <h6>Tytu Ogoszenia</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz tytu ogoszenia" />
                                </Col>

                                <Col lg={4}>
                                    <h6>Cena</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz cen" />
                                </Col>
                                <Col lg={4}>
                                    <h6>Marka</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz mark" />
                                </Col>
                                <Col lg={4}>
                                    <h6>Model</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz model" />
                                </Col>
                                <Col lg={4}>
                                    <h6>Wysoko Robocza</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz wysoko robocz" />
                                </Col>
                                <Col lg={4}>                                    <h6>Maksymalne obcienie</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz maksymalne obcienie" />
                                </Col>
                                <Col lg={4}>                                    <h6>Waga</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz wag" />
                                </Col>
                                <h2>Dodaj zdjcia</h2>
                                <p >Dodaj przynajmniej jedno zdjcie, gdy w przeciwnym razie Twoje ogoszenie bdzie wywietlane pod koniec listy ogosze..

                                </p>
                                <Col lg={5} className="add_img">
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>
                                    <button className="addImg"><img src={plusIcon} className="icon_plus" alt="" /></button>

                                </Col>
                                <Col lg={7} className="about_img">
                                    <p>Moesz doda do 20 zdj o wymiarach minimum 800x600 pikseli i wadze pliku nie wikszej ni 5MB, w formacie JPG, JPEG lub PNG
                                        Aby doda wiele zdj na raz, zaznaczaj je trzymajc klawisz Ctrl na klawiaturze.
                                        Pierwsza fotografia bdzie wywietlana jako gwne zdjcie nieruchomoci.
                                        Moesz zmieni kolejno zdj, przesuwajc miniaturki z wcinitym przyciskiem myszki.
                                        Idealne wymiary zdj wynosz 830x500 pixeli.</p>
                                </Col>

                                <Col lg={12} className="set_localization">
                                    <h2>Lokalizacja</h2>
                                    <h6>Miejscowo</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz miejscowo" />
                                    <h6>Dzielnica</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz dzielnic" />
                                    <h6>Ulica</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz ulic" />
                                </Col>
                                <Col lg={12} className="set_information mb-5">
                                    <h2>Szczegowe Informacje</h2>
                                    <h6>Opis</h6>
                                    <textarea name="description" id="" className="description form-control" cols="30" rows="10"></textarea>
                                    <div className="row">
                                        <Col lg={6}>
                                            <h6>Szeroko</h6>
                                            <input className="form-control" type="text" placeholder="Wpisz szeroko" />
                                        </Col>
                                        <Col lg={6}>
                                            <h6>Wysoko</h6>
                                            <input className="form-control" type="text" placeholder="Wpisz wysoko" />
                                        </Col>
                                        <Col lg={6}>
                                            <h6>Dugo</h6>
                                            <input className="form-control" type="text" placeholder="Wpisz dugo" />
                                        </Col>
                                        <Col lg={6}>
                                            <h6>Rodzaj napdu</h6>
                                            <input className="form-control" type="text" placeholder="Wpisz rodzaj napdu" />
                                        </Col>
                                    </div>
                                </Col>
                            </Row>

                            <Row>

                                <h2>Dane Kontaktowe</h2>
                                <Col lg={4}>
                                    <h6>Imi i Nazwisko</h6>
                                    <input className="form-control" type="text" placeholder="Wpisz imi i nazwisko" />
                                </Col>
                                <Col lg={4}>
                                    <h6>Telefon</h6>
                                    <input className="form-control" type="tel" placeholder="Wpisz numer telefonu" />
                                </Col>
                                <Col lg={4}>
                                    <h6>E-mail</h6>
                                    <input className="form-control" type="email" placeholder="Wpisz e-mail" />
                                </Col>
                            </Row>
                            <div className="agreement">
                                <p className="accept_regulations">Dodajc ogoszenie ackeptuj Regulamin serwisu Podnosniki.me Przyjmuj do wiadomoci, e Podnosniki.me wykorzystuje moje dane osobowe zgodnie z Polityk prywatnoci oraz Polityk dotyczc plikw cookie i podobnych technologii. Podnosniki.me wykorzystuje zautomatyzowane systemy i partnerw do analizowania, w jaki sposb korzystam z usug w celu zapewnienia odpowiedniej funkcjonalnoci produktu, treci, dostosowanych i opartych na zainteresowaniach reklam, jak rwnie ochrony przed spamem, zoliwym oprogramowaniem i nieuprawnionym korzystaniem z naszych usug.</p>
                                <input type="checkbox" name="" id="" className="accept_check" defaultChecked /> Wyraam zgod na uywanie przez Podnosniki.me rodkw komunikacji elektronicznej oraz telekomunikacyjnych urzdze kocowych w celu przesyania mi informacji handlowych oraz prowadzenia marketingu (np. newsletter, wiadomoci SMS) przez Podnosniki.me podmioty powizane i partnerw biznesowych.
                            </div>
                        </form>

                        <div className="add_button"><a href="" className="btn btn__map">Wstaw Ogoszenie</a></div>

                    </div>
                </Container>
            </section>


        </>
    )
}

export default Advertisement